@charset "UTF-8";

/* */
/* updated on 2017-03-30*/
/* */

@font-face {
  font-family: "Emphasis";

  src: local("Arial");
  unicode-range: U+2022, U+25CF;
}
@font-face {
  font-family: "Biaodian";

  src: local("PingFang SC"), local("Hiragino Sans GB"), local("Microsoft YaHei");
  unicode-range: U+3002, U+FF0C, U+3001, U+FF1A, U+FF1B, U+FF01, U+FF1F, U+300C, U+300D, U+300E, U+300F U+201C, U+2019, U+2018, U+2019 U+FF08, U+FF09, U+2E3A, U+2014 U+3010, U+3011, U+3016, U+3017, U+3014, U+3015, U+FF3B, U+FF3D, U+FF5B, U+FF5D, U+2026, U+2013, U+FF5E, U+007E, U+00B7, U+FE4F, U+300A, U+300B, U+3008, U+3009, U+FF3F, U+002F, U+FF0F;
}

html {
  font-size: 16px;
  font-family: "Emphasis", "Biaodian", -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Segoe UI", "Roboto", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "Noto Sans CJK", "WenQuanYi Micro Hei", sans-serif;
  line-height: 2;
  color: $color-font;

  @include _media( "lg" ){
    & {
      font-size: 16px;
    }
  }
  @include _media( "md" ){
    & {
      font-size: 15px;
    }
  }
  @include _media( "sm" ){
    & {
      font-size: 14px;
    }
  }
  @include _media( "xs" ){
    & {
      font-size: 14px;
    }
  }
}

html {
  box-sizing: border-box;

      -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}
@-ms-viewport {
  width: device-width;
}

@-ms-viewport { width:device-width; }
@-o-viewport { width:device-width; }
@viewport { width:device-width; }

::-webkit-scrollbar { -webkit-appearance: none;width: 8px;height: 8px; }
::-webkit-scrollbar-thumb { border: 3px solid transparent;background-color: rgba(0, 0, 0, .2); }
::-webkit-scrollbar-thumb:hover { background-color: rgba(0, 0, 0, .3); }
::-webkit-scrollbar-track { background-color: transparent; }

/**
* fix 'jumping scrollbar'
* https://aykevl.nl/2014/09/fix-jumping-scrollbar
*/
html {
  overflow-y: scroll;
}
:root {
  overflow-x: hidden;
  overflow-y: auto;
}
:root body {
  position: absolute;
}
body {
  width: 100vw;

  overflow: hidden;
}

hr {
  width: 100%;
  border: 1px $color-light solid;
  margin: 0;
}

ul {
  list-style-type: none;

  padding: 0;
  margin: 0;
}

a {
  display: block;

  background-color: transparent;

  color: inherit;
  text-decoration: none;

  -webkit-text-decoration-skip: objects;
}

img {
  border-style: none;
}


p {
  margin: 0;
}

/**
* reset the placeholder color of input
*/
::-webkit-input-placeholder {
  color: $color-deep;
}
:-moz-placeholder {
  opacity: 1;

  color: $color-deep;
}
::-moz-placeholder {
  opacity: 1;

  color: $color-deep;
}
:-ms-input-placeholder {
  color: $color-deep;
}

form {
  margin: 0;
}
input,
button,
label {
  border: none;
  outline: none;
  margin: 0;

  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  color: inherit;
}

/**
* correct the line-height of font-awesome 
*/
i.fa {
  line-height: inherit;
}
